import { initialize } from 'redux-form';
import * as types from '../constants/types/chat';

export const newChat = (id, visitorId) => ({
  type: types.newChat,
  payload: visitorId,
  meta: id,
});

export const deleteChat = id => ({
  type: types.deleteChat,
  meta: id,
});

export const chatVisitorLeaved = id => ({
  type: types.chatVisitorLeaved,
  meta: id,
});

export const chatEnded = id => ({
  type: types.chatEnded,
  meta: id,
});

export const tryCloseChat = id => ({
  type: types.tryCloseChat,
  meta: id,
});

export const newUnfinishedWrapup = unfinishedWrapup => ({
  type: types.newUnfinishedWrapup,
  payload: unfinishedWrapup,
});

export const onRequestedSecureForm = (id, token, rsaKey) => ({
  type: types.requestedSecureForm,
  payload: { token, rsaKey },
  meta: id,
});

export const onGetSecureFormContent = (id, token, content) => ({
  type: types.getSecureFormContent,
  payload: { token, content },
  meta: id,
});


export const initSendTranscriptForm = initialValues => initialize('sendTranscript', initialValues);

export const onUpdateIfPopTranslation = chatId => ({
  type: types.updateIfPopTranslation,
  payload: chatId,
});

export const setErrorMessageForChat = (id, errorMsg) => ({
  type: types.setErrorMessageForChat,
  payload: errorMsg,
  meta: id,
});
export const clearG2mStartUrl = id => ({
  type: types.clearG2mStartUrl,
  meta: id,
});
export const updateScrollTop = (id, scrollTop) => ({
  type: types.updateScrollTop,
  payload: scrollTop,
  meta: id,
});
export const updateIfShowNotify = (id, ifShowNotify) => ({
  type: types.updateIfShowNotify,
  payload: ifShowNotify,
  meta: id,
});
export const audioAndVideoWidthChanged = (id, info) => ({
  type: types.audioAndVideoWidthChanged,
  payload: info,
  meta: id,
});
export const setP2PChat = (id, p2pChat) => ({
  type: types.setP2PChat,
  payload: p2pChat,
  meta: id,
});



