import {
  EditorState,
} from 'draft-js';
import { connect } from 'react-redux';
import * as _ from 'lodash';
import flow from 'lodash/fp/flow';
import filter from 'lodash/fp/filter';
import each from 'lodash/fp/each';
import * as perf from 'src/utils/perf';
import * as reporter from 'src/utils/reporter';
import TextEditor from 'src/components/TextEditor/TextEditor';
import { isSendingMessageAllowed } from 'src/utils/common';
import * as selectors from '../reducers/selectors';
import * as actions from '../actions/chat';
import * as editorActions from '../actions/chatEditor';
import * as agentAction from '../actions/agent';
import * as configActions from '../actions/config';
import * as file from '../utils/file';
import * as editor from '../utils/editor';
import { chatUploadFile, uploadImage } from '../services/upload';
import { createGUID } from '../utils/guid';

const emptyEditorState = EditorState.createEmpty();
const emptyEditorStateFocused = editor.createEmpty();

let mapStateToProps = (state) => {
  const id = selectors.getSelectedChatId(state);
  if (!id) return { };
  const visitor = selectors.getVisitorByChatId(state, id);
  const agentId = selectors.getCurrentAgentId(state);
  const disabled = !isSendingMessageAllowed(visitor, agentId);
  let editorState = emptyEditorState;
  if (!disabled) {
    editorState = selectors.getChatEditorState(state, id) || emptyEditorStateFocused;
  }
  return ({
    id,
    disabled,
    sendToIds: selectors.getVisitorIdByChatId(state, id),
    editorState,
    sendType: selectors.getSendType(state),
    cannedMessages: selectors.getCannedMessagesWithShortcuts(state),
    visitor,
    agent: {
      name: selectors.getCurrentAgentName(state),
      email: selectors.getCurrentAgentEmail(state),
    },
    siteInfo: {
      uploadUrl: selectors.getFileUploadUrl(state),
      downloadUrl: selectors.getFileDownloadUrl(state),
      siteId: selectors.getSiteId(state),
      agentId: selectors.getCurrentAgentId(state),
      sessionId: selectors.getCurrentAgentSession(state),
      maxFileSize: selectors.getMaxFileSize(state),
      maxFileNum: selectors.getMaxFileNum(state),
    },
    selectedFontSize: selectors.getFontSize(state),
    textDirectionIfRTL: selectors.textDirectionIfRTL(state),
  });
};

const filterNonImageFiles = filter(_.negate(file.isImageFile));

const mapDispatchToProps = dispatch => ({
  onChange(id, editorState) {
    dispatch(editorActions.updateContent(id, editorState));
  },
  onSendOut(id, visitorId, editorState, { uploadUrl, siteId, agentId, sessionId, maxFileNum }) {
    const lines = editor.convertToList(editorState);
    if (lines.length > 1 || !/^\s*$/.test(lines[0])) {
      if (_.filter(lines, line => line instanceof Blob).length > maxFileNum) {
        dispatch(actions.setErrorMessageForChat(
          id,
          `Error sending image: The max number of images you can paste at a time is ${maxFileNum}`
        ));
      } else {
        dispatch(editorActions.updateContent(id, editor.createEmpty()));
        _.each(lines, (line) => {
          if (typeof line === 'string') {
            if (line !== null && !/^\s*$/.test(line)) {
              dispatch(agentAction.sendText(visitorId, line));
            }
          } else if (line instanceof Blob) {
            const imageId = createGUID();
            dispatch(agentAction.sendImageStart(visitorId, imageId, (guid) => {
              if (typeof guid === 'object' && guid.errorString) {
                dispatch(actions.setErrorMessageForChat(
                  id,
                  `Error sending image: ${guid.errorString}`
                ));
              } else {
                uploadImage(line, uploadUrl, guid, siteId, sessionId, visitorId, agentId);
              }
            }));
          } else if (process.env.NODE_ENV !== 'production') {
            reporter.error(new Error('unkown line'), 'ChatTextEditor', 82, 0);
          }
        });
      }
    }
  },
  onDrop(id, files, editorState, { uploadUrl, siteId, sessionId, maxFileSize }, visitorId) {
    const [toolarge, others] =
      _.partition(files, file.isFileExceedMaxSize(maxFileSize * (1024 ** 2)));
    if (toolarge.length > 0) {
      dispatch(actions.setErrorMessageForChat(
        id,
        `Error sending file: The max file size is ${maxFileSize} MB`
      ));
    }
    const images = others.filter(file.isImageFile);
    if (images.length > 0) {
      dispatch(editorActions.insertImages(
        id,
        images,
        editorState
      ));
    }
    flow(
      filterNonImageFiles,
      each((f) => {
        chatUploadFile(f, uploadUrl, sessionId, visitorId, siteId, console.log.bind(console))
          .then((resp) => {
            if (resp && (resp.data !== 'success')) {
              dispatch(actions.setErrorMessageForChat(id, `Error sending file: ${resp.data}`));
            }
          })
          /** @todo handle error more gracefully */
          .catch((err) => {
            dispatch(actions.setErrorMessageForChat(id, err));
          });
      })
    )(others);
  },
  onSendTypeSelect(type) {
    dispatch(configActions.updateSendType(type));
    dispatch(agentAction.setPreference());
  },
  onCannedMessageUse(chatId, cannedMessageId) {
    dispatch(agentAction.agentUseCannedMessage(chatId, cannedMessageId));
  },
});

if (process.env.NODE_ENV !== 'production') {
  mapStateToProps = perf.markFunctionCall('ChatTextEditor: mapStateToProp', mapStateToProps);
}

export default connect(mapStateToProps, mapDispatchToProps)(TextEditor);



