import React from 'react';
import { Editor, EditorState, RichUtils } from 'draft-js';
import Immutable from 'immutable';
import * as _ from 'lodash';
import classnames from 'classnames';
import 'draft-js/dist/Draft.css';
import * as fontSize from 'src/constants/enums/fontSize';
import * as file from 'src/utils/file';
import * as enumSendType from 'src/constants/enums/sendType';
import { empty } from 'src/utils/func';
import * as browser from 'src/utils/browser';
import * as editor from 'src/utils/editor';
import { shouldComponentUpdateGen } from 'src/utils/helper';
import * as helper from 'src/utils/common';
import style from './TextEditor.css';
import Media from './Media';
import Selection from './Selection';
import SendButton from './SendButton';

/** Whether editor can use `drag and drop` feature to upload file */
const isUploadSupport = browser.isDraggableSupport && browser.isFileReaderSupport;

const mediaBlockRenderer = (block) => {
  if (block.getType() !== 'atomic') return null;
  return {
    component: Media,
    editable: false,
  };
};

const allowedChar = /\w/;
/**
 * Whether this char is part of token
 *
 * @param {string} char - Current character
 * @return {boolean} Test result
 */
const isCharAllowed = char => allowedChar.test(char);
/**
 * Find first char that is not part of this token
 *
 * @param {string} text - Search text
 * @param {number} start - Start index
 * @param {number} step - Step, should be either 1 or -1
 *
 * @return {number} index of first invalid char, could be -1 or text.length if all satisified
 */
const getIndex = (text, start, step) => {
  let i = start;
  const length = text.length;
  for (;i < length && i >= 0 && isCharAllowed(text[i]); i += step);
  return i;
};

/**
 * Get position of token which contains current cursor; Will return `null` if current state is not
 * cursor.
 *
 * @param {object} editorState - Current EditorState
 * @return {{ start: number, end: number, text: string, key: string }} Info of current token.
 */
const getTokenPosition = (editorState) => {
  const cursor = editor.getCursorPosition(editorState);
  if (cursor === null) return cursor;
  const text = editor.getText(editorState, cursor.key);
  const i = getIndex(text, cursor.index - 1, -1);
  const j = getIndex(text, cursor.index, 1);
  return { start: i, end: j, text, key: cursor.key };
};

/**
 * Get token that contains current cursor. Will return `null` if current state is not cursor.
 *
 * @param {object} editorState - Current EditorState
 * @return {?string} Token that contains current cursor; `null` if current state is not cursor.
 */
const getTokenText = (editorState) => {
  const position = getTokenPosition(editorState);
  if (position === null) return null;
  if (position.text[position.start] !== '#') return null;
  return position.text.substr(position.start, position.end - position.start);
};

const findCannedMessages = (token, messages) => {
  if (!token) return [];
  const key = token.slice(1); // remove '#' at the beginning
  return _.filter(messages, msg => msg.shortcut.toLowerCase().indexOf(key.toLowerCase()) === 0);
};


function cleanSelection() {
  if (process.env.IS_WEB_APP && browser.isIE) {
    /**
     * Workaround for IE browsers. Details, check issue 18804
     *
     * Explaination:
     * IE forgets to re-draw the area to hide <Select />, causing some part still displaying.
     * Following workaround force the browser to re-draw.
     * This is bad for performance, but works to avoid this bug.
     */
    setTimeout(() => {
      if (document.querySelector('#workaround')) {
        document.body.removeChild(document.querySelector('#workaround'));
        return;
      }
      const element = document.createElement('div');
      element.id = 'workaround';
      const elementStyle = element.style;
      elementStyle.position = 'fixed';
      elementStyle.top = 0;
      elementStyle.bottom = 0;
      elementStyle.left = 0;
      elementStyle.right = 0;
      elementStyle.backgroundColor = 'transparent';
      document.body.insertBefore(element, document.body.firstChild);
    }, 0);
  }
}

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'TextEditor',
  ['sendType', 'height', 'disabled', 'selectedFontSize', 'textDirectionIfRTL'],
  ['editorState', 'isDragging', 'selected']
);

const getFontSizeClass = type => ({
  [fontSize.small]: style.chatContentsmall,
  [fontSize.middle]: style.chatContentmiddle,
  [fontSize.large]: style.chatContentlarge,
})[type];

class TextEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isDragging: false,
      cannedMessages: [],
      selected: undefined,
      /** @todo add default state when not provided */
      editorState: props.editorState,
    };
    this.onChange = this.onChange.bind(this);
    this.handleReturn = this.handleReturn.bind(this);
    this.onUpArrow = this.onUpArrow.bind(this);
    this.onDownArrow = this.onDownArrow.bind(this);
    this.onArrowMove = this.onArrowMove.bind(this);
    this.onTab = this.onTab.bind(this);
    this.onEscape = this.onEscape.bind(this);
    this.selectCannedMessage = this.selectCannedMessage.bind(this);
    this.handleKeyCommand = this.handleKeyCommand.bind(this);
    this.onScroll = this.onScroll.bind(this);
    if (isUploadSupport && props.onDrop) {
      this.showDragging = this.showDragging.bind(this);
      this.hideDragging = this.hideDragging.bind(this);
      this.onDrop = this.onDrop.bind(this);
    } else {
      this.showDragging = this.hideDragging = this.onDrop = empty;
    }
    this.updateEditorStateTimer = null;
    this.scrollTop = 0;
  }

  componentDidMount() {
    this.editor.refs.editor.style.height = `${this.props.height}px`;
    this.editor.focus();
    this.editor.refs.editor.addEventListener('scroll', this.onScroll);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.editorState === nextProps.editorState) return;
    this.shouldScroll = this.shouldScroll || editor.getHasFocus(nextProps.editorState);
    this.setState({ editorState: nextProps.editorState });
  }

  shouldComponentUpdate(nextProps, nextState) {
    return shouldComponentUpdateHelper(this.props, nextProps, this.state, nextState);
  }

  componentDidUpdate(prevProps, prevState) {
    this.editor.refs.editor.style.height = `${this.props.height}px`;
    if (prevProps.id !== this.props.id) this.editor.focus();
    if (!this.state.selected && prevState.selected) cleanSelection();
    if (process.env.IS_WEB_APP && browser.isFirefox) {
      /**
       * Workaround for Firefox browsers. Details, check issue 18738
       */
      const isHavingFocus =
        !editor.getHasFocus(prevState.editorState) && editor.getHasFocus(this.state.editorState);
      if (isHavingFocus) {
        this.editor.focus();
      }
    }
    if (this.shouldScroll) {
      /**
       * scroll to cursor when pasted. Draft-js will not do that automatically, instead,
       * manually calculate the offset and scroll the position.
       */
      this.shouldScroll = false;
      const selection = window.getSelection();
      if (!selection || selection.rangeCount <= 0) return;
      const rect = selection.getRangeAt(0).getClientRects()[0];
      if (!rect) return;
      const containerRect = this.container.getBoundingClientRect();
      const offset = (rect.bottom - containerRect.bottom) + 45;
      this.editor.refs.editor.scrollTop = this.scrollTop + offset;
    }
  }

  componentWillUnmount() {
    this.editor.refs.editor.removeEventListener('scroll', this.onScroll);
  }

  onChange(editorState) {
    if (editor.getHasFocus(editorState)) {
      if (this.updateEditorStateTimer) clearTimeout(this.updateEditorStateTimer);
      const token = getTokenText(editorState);
      const cannedMessages = findCannedMessages(token, this.props.cannedMessages);
      if (cannedMessages.length !== this.state.cannedMessages.length ||
      this.state.cannedMessages.every((msg, i) => msg !== cannedMessages[i])) {
        this.setState({
          cannedMessages,
          selected: cannedMessages[0],
          editorState,
        });
      }
    } else {
      if (this.updateEditorStateTimer) return;
      this.updateEditorStateTimer = setTimeout(() => {
        this.updateEditorStateTimer = null;
        this.props.onChange(this.props.id, editorState);
      }, 20); /** @todo do not use magic number here */
    }
  }

  onScroll(event) {
    this.scrollTop = event.target.scrollTop;
  }

  onDrop(files) {
    const { onDrop, id, siteInfo, sendToIds } = this.props;
    const { editorState } = this.state;
    onDrop(id, files, editorState, siteInfo, sendToIds);
    // to prevent Draft-js display plain text file directly in editor
    return 'handled';
  }

  onArrowMove(event, direction) {
    const { cannedMessages, selected } = this.state;
    if (!selected) return;
    event.preventDefault();
    const length = cannedMessages.length;
    const index = _.findIndex(cannedMessages, msg => msg === selected);
    this.setState({
      selected: cannedMessages[((index + direction) + length) % length],
    });
  }

  onDownArrow(event) {
    this.onArrowMove(event, 1);
  }

  onUpArrow(event) {
    this.onArrowMove(event, -1);
  }

  onTab(event) {
    if (this.selectCannedMessage()) event.preventDefault();
  }

  onEscape(event) {
    const { selected } = this.state;
    if (!selected) return;
    event.preventDefault();
    this.setState({ selected: undefined, cannedMessages: [] });
  }

  handleKeyCommand(command) {
    const { onChange, id } = this.props;
    const state = RichUtils.handleKeyCommand(this.state.editorState, command);
    if (!state) return 'unhandled';
    onChange(id, state);
    return 'handled';
  }

  handleReturn(event) {
    if (this.selectCannedMessage()) return 'handled';
    const ctrlEnter = !event.altKey && event.ctrlKey && !event.metaKey && !event.shiftKey;
    const { sendType, onSendOut, id, sendToIds, siteInfo } = this.props;
    if ((ctrlEnter && sendType === enumSendType.ctrlEnter) ||
    (!ctrlEnter && sendType === enumSendType.enter)) {
      onSendOut(id, sendToIds, this.state.editorState, siteInfo);
      return 'handled';
    }
    return 'unhandled';
  }

  selectCannedMessage(selected = this.state.selected) {
    if (!selected) return false;
    const { id, visitor, agent, onCannedMessageUse } = this.props;
    const editorState = this.state.editorState;
    const position = getTokenPosition(editorState);
    if (!position) return false;
    if (selected && selected.id && onCannedMessageUse) {
      onCannedMessageUse(id, selected.id);
      this.shouldScroll = true;
    }
    this.setState({
      selected: undefined,
      cannedMessages: [],
      editorState: editor.replaceText(
        editorState,
        position.key,
        position.start,
        position.end,
        helper.replaceMacros(selected.content, visitor, agent)
      ),
    });
    return true;
  }

  showDragging(event) {
    if (Array.from(event.dataTransfer.types).some(type => type === 'Files') > 0) {
      this.setState({ isDragging: true });
    }
  }

  hideDragging() {
    this.setState({ isDragging: false });
  }

  render() {
    const { sendType,
      onSendTypeSelect,
      onSendOut,
      sendToIds,
      id,
      siteInfo,
      disabled,
      selectedFontSize,
      textDirectionIfRTL,
      disablePaste,
    } = this.props;
    const { isDragging, cannedMessages, selected, editorState } = this.state;
    const isFocused = editor.getHasFocus(editorState);
    return (
      <div
        className={classnames(
          style.container,
          disabled && style.disabled,
          !disabled && isFocused && style.focused,
          !disabled && isDragging && style.dragging,
          getFontSizeClass(selectedFontSize),
          textDirectionIfRTL ? style.directionRTL : ''
        )}
        onDragOver={!disabled && this.showDragging}
        onDragLeave={!disabled && this.hideDragging}
        onDragEnd={!disabled && this.hideDragging}
        onDrop={!disabled && this.hideDragging}
        ref={(container) => { this.container = container; }}
      >
        <Selection
          display={!disabled && isFocused}
          selected={selected}
          cannedMessages={cannedMessages}
          onSelect={this.selectCannedMessage}
          cleanSelection={cleanSelection}
        />
        <Editor
          editorState={editorState}
          onChange={this.onChange}
          spellCheck
          handleKeyCommand={this.handleKeyCommand}
          blockRendererFn={mediaBlockRenderer}
          handleReturn={this.handleReturn}
          onUpArrow={this.onUpArrow}
          onDownArrow={this.onDownArrow}
          onTab={this.onTab}
          onEscape={this.onEscape}
          ref={(element) => { this.editor = element; }}
          handleDroppedFiles={(selection, files) => this.onDrop(files)}
          stripPastedStyles
          handlePastedFiles={(files) => {
            if (disablePaste) return;
            this.onDrop(files);
          }}
          handlePastedText={() => { this.shouldScroll = true; }}
          readOnly={disabled}
        />
        <SendButton
          id={id}
          sendToIds={sendToIds}
          editorState={editorState}
          className={style.buttonGroup}
          onSelect={onSendTypeSelect}
          type={sendType}
          onClick={(i, s, e) => onSendOut(i, s, e, siteInfo)}
          disabled={disabled}
        />
      </div>
    );
  }
}

TextEditor.propTypes = {
  /** chat id */
  id: React.PropTypes.number.isRequired,

  /** visitor id, use array of ids if text should be sent to multiple visitors/agents  */
  sendToIds: React.PropTypes.oneOfType([
    React.PropTypes.number,
    React.PropTypes.arrayOf(React.PropTypes.number.isRequired),
  ]).isRequired,
  /** the url of upload */
  siteInfo: React.PropTypes.shape({}),

  /** function to update `editorState` */
  onChange: React.PropTypes.func.isRequired,
  /** function to inform when message should be sent out */
  onSendOut: React.PropTypes.func.isRequired,
  /** editorState for current <Editor> */
  editorState: React.PropTypes.instanceOf(EditorState).isRequired,
  /** determines how should message be sent out, either `enter` or `ctrl + enter` */
  sendType:
    React.PropTypes.oneOf(Object.keys(enumSendType).map(key => enumSendType[key])).isRequired,
  /** function to inform when file drag and drop. Not provided means no drag and drop feature  */
  onDrop: React.PropTypes.func,
  /** canned message list */
  cannedMessages: React.PropTypes.arrayOf(React.PropTypes.shape({})),
  /** function to inform when sendType changed */
  onSendTypeSelect: React.PropTypes.func.isRequired,
  height: React.PropTypes.number,
  /** disable functionality of <TextEditor /> */
  disabled: React.PropTypes.bool,
  agent: React.PropTypes.shape({
    name: React.PropTypes.string.isRequired,
    email: React.PropTypes.string.isRequired,
  }),
  visitor: React.PropTypes.instanceOf(Immutable.Map),
  /** callback when canned message is inserted */
  onCannedMessageUse: React.PropTypes.func,
  selectedFontSize: React.PropTypes.string.isRequired,
  textDirectionIfRTL: React.PropTypes.bool,
  disablePaste: React.PropTypes.bool,
};

/* istanbul ignore next */ TextEditor.examples = {
  'editable component example (ctrl + enter send out)': {
    description: [
      'Use a parent component to handle outside state. This is an example showing how should ',
      'each callback look like.',
    ].join(''),
    component:
      React.createElement(
        class Example extends React.PureComponent { // eslint-disable-line
          constructor() {
            super();
            this.state = { editorState: EditorState.createEmpty(), sendType: enumSendType.enter };
            this.onChange = this.onChange.bind(this);
            this.onDrop = this.onDrop.bind(this);
            this.onSendTypeSelect = this.onSendTypeSelect.bind(this);
          }

          // update editorState
          onChange(id, editorState) {
            this.setState({ editorState });
          }

          onDrop(id, files) {
            let state = this.state.editorState;
            let finished = 0;
            console.log(files);
            files.forEach((f) => {
              if (file.isImageFile(f)) {
                const reader = new FileReader();
                reader.onload = () => {
                  finished += 1;
                  state = editor.insertImage(state, reader.result, { file: f });
                  if (finished === files.length) this.setState({ editorState: state });
                  else console.log('here');
                };
                reader.readAsDataURL(f);
              } else {
                console.log('Not image file', f.name);
                finished += 1;
              }
            });
          }

          onSendTypeSelect(sendType) {
            this.setState({ sendType });
          }

          render() {
            const { editorState } = this.state;
            return (
              <TextEditor
                id="1"
                onChange={this.onChange}
                onSendOut={() => {
                  console.log(editor.convertToList(this.state.editorState));
                }}
                sendType={this.state.sendType}
                editorState={editorState}
                height={200}
                onDrop={this.onDrop}
                cannedMessages={[
                  { shortcut: 'hello', content: 'hello world!' },
                  { shortcut: 'end', content: 'goodbye!' },
                  { shortcut: 'veryveryveryverylongkey', content: 'short value' },
                  { shortcut: 'key', content: 'very very very very very very very very very very very very long value' },
                  { shortcut: 'key1', content: '1' },
                  { shortcut: 'key2', content: '2' },
                  { shortcut: 'key3', content: '3' },
                  { shortcut: 'key4', content: '4' },
                  { shortcut: 'kind', content: 'you are so kind' },
                ]}
                onSendTypeSelect={this.onSendTypeSelect}
              />
            );
          }
        },
      ),
  },
};

export default TextEditor;



