import * as config from '../constants/types/config';

/**
 * Action to change order of visitor list columns.
 *
 * @param {boolean} leftToRight - Whether column was dragged from left to right.
 * @param {number} startEnumColumn - enumColumn of columnConfig in start position
 * @param {number} startCustomVariableId - customVariableId of columnConfig in start position
 * @param {number} endEnumColumn - enumColumn of columnConfig in end position
 * @param {number} endCustomVariableId - customVariableId of columnConfig in end position
 * @return {object} action. Each column config requires both `enumColumn` and `customVariable` to
 * determine, thus both will be provided in payload, with prefix `start` and `end` indicating the
 * order position.
 */
export const changeVisitorListColumnOrder = (
  leftToRight,
  startEnumColumn,
  startCustomVariableId,
  endEnumColumn,
  endCustomVariableId
) => ({
  type: config.changeVisitorListColumnOrder,
  payload: {
    startEnumColumn,
    startCustomVariableId,
    endEnumColumn,
    endCustomVariableId,
  },
  meta: {
    leftToRight,
  },
});

export const changeVisitorListColumnSize = (enumColumn, customVariableId, width) => ({
  type: config.changeVisitorListColumnSize,
  payload: width,
  meta: {
    enumColumn,
    customVariableId,
  },
});

export const updatePreference = preference => ({
  type: config.updatePreference,
  payload: preference,
});

export const updateSendType = sendType => ({
  type: config.changeSendType,
  payload: sendType,
});

export const updateCustomFilter = customFilter => ({
  type: config.updateCustomFilter,
  payload: customFilter,
});



