export const readAsDataURL = file => new Promise((resolve, reject) => {
  const reader = new FileReader();
  reader.onload = () => {
    resolve(reader.result, file);
  };
  reader.onerror = reject;
  reader.readAsDataURL(file);
});

export const isImageFile = file => file.type.indexOf('image') === 0;

export const isFileExceedMaxSize = maxSize => file => file.size > maxSize;



