import {
  EditorState,
  AtomicBlockUtils,
  Entity,
  convertToRaw,
  SelectionState,
  Modifier,
} from 'draft-js';
import * as _ from 'lodash';
import * as enums from '../constants/enums';

export const getHasFocus = editorState => editorState.getSelection().getHasFocus();
/**
 * Replace range of text with new one and *focus at the end of new text*
 *
 * @param {object} editorState - Current EditorState
 * @param {string} key - Key of ContentBlock where old text exists
 * @param {number} start - Start index of old text
 * @param {number} end - End index of old text
 * @param {string} text - Text that should be used instead
 * @return {object} Modified EditorState after replacing the text and focus at the end of new text
 */
export const replaceText = (editorState, key, start, end, text) => {
  const selection = SelectionState.createEmpty('canned-message').merge({
    anchorKey: key,
    anchorOffset: start,
    focusKey: key,
    focusOffset: end,
  });
  const content = Modifier.replaceText(editorState.getCurrentContent(), selection, text);
  return EditorState.forceSelection(
    EditorState.push(editorState, content, 'change-block-data'),
    SelectionState.createEmpty('canned-message').merge({
      anchorKey: key,
      anchorOffset: start + text.length,
      focusKey: key,
      focusOffset: start + text.length,
    })
  );
};

export const getCursorPosition = (editorState) => {
  const selection = editorState.getSelection();
  if (!selection.isCollapsed()) return null;
  const key = selection.getStartKey();
  const index = selection.getStartOffset();
  const text = editorState.getCurrentContent().getBlockForKey(key).getText();
  return { key, index, text };
};

export const getText = (editorState, key) =>
  editorState.getCurrentContent().getBlockForKey(key).getText();

export const convertToList = (editorState) => {
  const state = convertToRaw(editorState.getCurrentContent());
  const result = state.blocks
    .map((block) => {
      if (block.type === 'unstyled') return block.text;
      if (block.type === 'atomic') {
        return block.entityRanges
          // only one image per line
          .map(entity => state.entityMap[entity.key].data.payload.file)[0];
      }
      return undefined;
    })
    .reduce((previous, current) => {
      if (current === undefined) return previous;
      if (previous.length === 0) return [current];
      const last = previous[previous.length - 1];
      if (_.isString(current) && _.isString(last)) {
        previous[previous.length - 1] = `${last}\n${current}`; // eslint-disable-line
        return previous;
      }
      return previous.concat(current);
    }, []);
  return result;
};

export const insertImage = (editorState, src, payload) => {
  const entityKey = Entity.create(
    enums.mediaType.image,
    'IMMUTABLE',
    { src, payload },
  );
  return AtomicBlockUtils.insertAtomicBlock(
    editorState,
    entityKey,
    ' '
  );
};

export const moveFocusToEnd = editorState => EditorState.moveFocusToEnd(editorState);

export const createEmpty = () => EditorState.moveFocusToEnd(EditorState.createEmpty());

export const insertText = (text, editorState) => {
  const selection = editorState.getSelection();
  const contentState = editorState.getCurrentContent();
  let nextEditorState = EditorState.createEmpty();
  if (selection.isCollapsed()) {
    const nextContentState = Modifier.insertText(contentState, selection, text);
    nextEditorState = EditorState.push(
      editorState,
      nextContentState,
      'insert-cannedMessage'
    );
  } else {
    const nextContentState = Modifier.replaceText(contentState, selection, text);
    nextEditorState = EditorState.push(
      editorState,
      nextContentState,
      'insert-cannedMessage'
    );
  }
  return EditorState.forceSelection(nextEditorState, nextEditorState.getSelection());
};



