import axios from 'axios';
import { formatAgentChatUploadUrl, formatAgentChatDownloadUrl } from '../utils/helper';
import { createGUID } from '../utils/guid';

export function agentChatUpload(
  file,
  uploadUrlBase,
  downloadUrlBase,
  siteId,
  sessionId,
  onUploadProgress,
) {
  const uploadUrl = formatAgentChatUploadUrl(
    uploadUrlBase,
    sessionId,
    siteId,
    1,
    createGUID()
  );
  const data = new FormData();
  data.append('file', file);
  const config = { };
  if (onUploadProgress) {
    config.onUploadProgress = (progressEvent) => {
      const progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
      onUploadProgress(progress);
    };
  }
  return axios.post(uploadUrl, data, config)
    .then(respond => ({
      url: formatAgentChatDownloadUrl(
        downloadUrlBase,
        sessionId,
        siteId,
        1,
        respond.data.guid,
        file.name
      ),
      guid: respond.data.guid,
    }));
}

export function chatUploadFile(
  file,
  uploadUrlBase,
  sessionId,
  visitorId,
  siteId,
  onUploadProgress
) {
  const uploadUrl = `${uploadUrlBase}?sessionId=${sessionId}&visitorId=${visitorId}&siteId=${siteId}`;
  const data = new FormData();
  data.append('file', file);
  const config = { };
  if (onUploadProgress) {
    config.onUploadProgress = (progressEvent) => {
      const progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
      onUploadProgress(progress);
    };
  }
  return axios.post(uploadUrl, data, config);
}

const readBlobToArrayBuffer = blob =>
  new Promise((resolve, reject) => {
    try {
      const fileReader = new FileReader();
      fileReader.onload = () => resolve(fileReader.result);
      fileReader.readAsArrayBuffer(blob);
    } catch (e) {
      reject(e);
    }
  });

export function uploadImage(
  image,
  uploadUrlBase,
  guid,
  siteId,
  sessionId,
  visitorId,
  agentId,
  onUploadProgress,
) {
  const uploadUrl = `${uploadUrlBase}?siteId=${siteId}&operatorId=${agentId}&visitorId=${visitorId}&sessionId=${sessionId}&isImage=true&guid=${guid}&fileDisplayName=${guid}.png`;
  const config = { };
  if (onUploadProgress) {
    config.onUploadProgress = (progressEvent) => {
      const progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
      onUploadProgress(progress);
    };
  }
  return readBlobToArrayBuffer(image).then(
    data => axios.post(uploadUrl, data, config)
  );
}





