import React from 'react';
import Button from 'src/components/Button/Button';
import * as sendType from 'src/constants/enums/sendType';
import SelectionLine from './SelectionLine';
import Icon from '../Icon/Icon';
import { icons } from '../../constants/enums';
import style from './TextEditor.css';


class SendButton extends React.Component {
  constructor(props) {
    super(props);
    this.state = { toggle: false };
    this.onToggle = this.onToggle.bind(this);
    this.onSelect = this.onSelect.bind(this);
  }

  componentDidMount() {
    this.listener = (event) => {
      if (this.selection && !this.selection.contains(event.target) && this.state.toggle) {
        this.setState({ toggle: false });
      }
    };
    window.addEventListener('click', this.listener, false);
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.listener);
  }

  onToggle(e) {
    e.stopPropagation();
    this.setState({ toggle: !this.state.toggle });
  }

  onSelect(type) {
    this.props.onSelect(type);
    this.setState({ toggle: false });
  }

  render() {
    const { className, type, onClick, id, sendToIds, editorState, disabled } = this.props;
    return (
      <div ref={(element) => { this.selection = element; }}>
        <div
          style={{ display: !disabled && this.state.toggle ? 'block' : 'none' }}
          className={style.sendType}
        >
          <SelectionLine
            text="Press Enter to Send"
            onClick={() => this.onSelect(sendType.enter)}
            selected={type === sendType.enter}
          />
          <SelectionLine
            text="Press Ctrl+Enter to Send"
            onClick={() => this.onSelect(sendType.ctrlEnter)}
            selected={type === sendType.ctrlEnter}
          />
        </div>
        <div className={className}>
          <Button
            type="primary"
            text="Send"
            onClick={() => onClick(id, sendToIds, editorState)}
            disabled={disabled}
          />
          <Button
            type="primary"
            disabled={disabled}
            className={style.arrowDownIcon}
            icon={
              <Icon
                type={icons.arrowDown}
              />
            }
            onClick={this.onToggle}
          />
        </div>
      </div>
    );
  }
}

SendButton.propTypes = {
  id: React.PropTypes.number.isRequired,
  sendToIds: React.PropTypes.oneOfType([
    React.PropTypes.number,
    React.PropTypes.arrayOf(React.PropTypes.number.isRequired),
  ]).isRequired,
  editorState: React.PropTypes.shape({}).isRequired,
  className: React.PropTypes.string.isRequired,
  onSelect: React.PropTypes.func.isRequired,
  onClick: React.PropTypes.func.isRequired,
  type: React.PropTypes.oneOf(Object.keys(sendType).map(key => sendType[key])).isRequired,
  disabled: React.PropTypes.bool,
};

export default SendButton;



