import Immutable from 'immutable';
import _ from 'lodash';
import * as fontSize from '../../constants/enums/fontSize';
import * as selector from '../../reducers/selectors';
import * as messageType from '../../constants/enums/messageType';
import { agentStatus, allAvailableId } from '../../constants/enums';
import * as permissionType from '../../constants/enums/liveChatPermission';

const previousState = {};
function getMessages(state, chat) {
  const currentAgent = selector.getCurrentAgent(state);
  return chat.get('messages').map(msgs =>
     msgs.map((msg, i) => {
       const newMessage = Object.assign({}, msg, {
         id: `${msg.agentId}_${i}`,
         type: msg.agentId === currentAgent.id ? messageType.agent : messageType.visitor,
         sender: selector.getAgentById(state, msg.agentId).name,
       });
       return newMessage;
     })
  );
}

function generateContentObject(state, obj, agentOrDpt, isAgent = true) {
  if (_.isUndefined(agentOrDpt)) {
    return previousState.content;
  }
  const key = isAgent ? agentOrDpt.id : -agentOrDpt.id;
  previousState.content = Object.assign({}, obj);
  if (_.isPlainObject(agentOrDpt)) {
    previousState.content.id = key;
    previousState.content.chatTo = agentOrDpt.name;
    const chat = selector.getAgentChat(state, key);
    if (chat) {
      previousState.content.messages = getMessages(state, chat);
      previousState.content.scrollTop = chat.get('scrollTop');
      previousState.content.errorMessageForChat = chat.get('errorMessageForChat');
    }
  }
  return previousState.content;
}

export function mapToChatContentState(state) {
  const contentState = {
    id: -1,
    selectedFontSize: fontSize.middle,
    chatTo: '',
    typingMessage: '',
    scrollTop: -1,
    messages: Immutable.List(),
    textDirectionIfRTL: selector.textDirectionIfRTL(state),
    errorMessageForChat: '',
  };
  const selectedInfo = selector.getSelectedIdForAgents(state).toString().split('_');
  if (selectedInfo.length > 1) {
    /* agentId */
    const selectedAgent = selector.getAgentById(state, _.parseInt(selectedInfo[1]));
    return generateContentObject(state, contentState, selectedAgent);
  } else if (selectedInfo[0] !== '0') {
    const selectedDpt = selector.getDepartmentById(state, Math.abs(selectedInfo[0]));
    return generateContentObject(state, contentState, selectedDpt, false);
  }
  /* all Available */
  return generateContentObject(state, contentState, {
    id: allAvailableId,
    name: 'All Available',
  }, false);
}

function getAgentAvatarUrl(state, agentId) {
  return `${selector.getAdminAgentAvatarUrl(state)}?siteId=${selector.getSiteId(state)}&operatorId=${agentId}`;
}

export function mapToChatHeader(state) {
  const strId = selector.getSelectedIdForAgents(state).toString();
  const ids = strId.split('_');
  const agentOrDepartment = (ids.length > 1 ?
    selector.getAgentById(state, _.parseInt(ids[1])) :
    selector.getDepartmentById(state, Math.abs(ids[0])));
  if (!_.isUndefined(previousState.header) &&
      _.isUndefined(agentOrDepartment) &&
      (ids.length !== 1 || Math.abs(ids[0]) !== allAvailableId)) {
    const newState = { agent: Object.assign({}, previousState.header.agent) };
    newState.agent.ifDisableKickOff = true;
    newState.agent.ifDisableSetAway = true;
    return newState;
  }
  const intId = _.parseInt(ids.length > 1 ? ids[1] : ids[0]);
  previousState.header = {
    agent: {
      id: intId,
      ifCurrentAgent: intId === selector.getCurrentAgentId(state),
      avatarLink: ids.length > 1 ? getAgentAvatarUrl(state, ids[1]) : '',
      name: _.isUndefined(agentOrDepartment) ? 'All Available' : agentOrDepartment.name,
      ifDisableKickOff: (
        !selector.getPermission(state, permissionType.logOff) ||
        ids.length === 1 ||
        (ids.length > 1 && selector.getAgentById(state, intId).status === agentStatus.offline)),
      ifDisableSetAway: (
        !selector.getPermission(state, permissionType.setAway) ||
        ids.length === 1 ||
        (ids.length > 1 && selector.getAgentById(state, intId).status !== agentStatus.online)),
    },
  };
  return previousState.header;
}

/* chat text editor */
export const getAgentOrDepartmentId = (state) => {
  const selectedInfo = selector.getSelectedIdForAgents(state).toString().split('_');
  if (selectedInfo.length > 1) {
    return [true, _.parseInt(selectedInfo[1])];
  }
  return [false, _.parseInt(selectedInfo[0])];
};

export const ifChatIsAvalable = (state, isAgent, id) => {
  if (isAgent) {
    if (!selector.getPermission(state, permissionType.chatWithOtherAgents)) return false;
    const agent = selector.getAgentById(state, id);
    if (agent && id !== selector.getCurrentAgentId(state)) {
      return agent.status !== agentStatus.offline;
    }
    return false;
  }
  return true;
};

/* get sendToIds */
export const getSendToIds = (state, isAgent, id) => {
  if (isAgent) return [id];
  const currentAgentId = selector.getCurrentAgentId(state);
  const ids = [];
  if (id === allAvailableId) {
    _.each(selector.getAgents(state), (agent) => {
      if (currentAgentId !== agent.id && agent.status !== agentStatus.offline) {
        ids.push(agent.id);
      }
    });
    return ids;
  }
  const department = selector.getDepartmentById(state, id);
  if (!department) return [];
  _.each(department.agentIds, (agentId) => {
    const temp = selector.getAgentById(state, agentId);
    if (!temp) return;
    if (currentAgentId !== temp.id && temp.status !== agentStatus.offline) {
      ids.push(agentId);
    }
  });
  return ids;
};

export const filterSendToIds = (chatId, ids, isAgent) => {
  if (!isAgent) {
    return ids.concat(chatId);
  }
  return ids;
};

export const isActiveStatus = (state, isAgent, id) => {
  const currId = Math.abs(id);
  const obj = (isAgent ?
    selector.getAgentById(state, currId) :
    selector.getDepartmentById(state, currId));
  if (_.isUndefined(obj) && currId === allAvailableId) return true;
  return !_.isUndefined(obj);
};



