import * as types from '../constants/types/agentChat';

export const updateContent = (id, editorState) => ({
  type: types.updateContent,
  payload: editorState,
  meta: id,
});
export const appendOwnMessage = (appendIds, line) => ({
  type: types.appendOwnMessage,
  payload: line,
  meta: appendIds,
});
export const updateScrollTop = (id, scrollTop) => ({
  type: types.updateScrollTop,
  payload: scrollTop,
  meta: id,
});
export const appendFile = (appendIds, url, fileName, isImage) => ({
  type: types.appendFile,
  payload: { fileName, url, isImage },
  meta: appendIds,
});
export const setErrorMessageForAgentChat = (id, errorMsg) => ({
  type: types.setErrorMessageForAgentChat,
  payload: errorMsg,
  meta: id,
});



