import { EditorState } from 'draft-js';
import * as editor from 'src/utils/editor';
import * as types from '../constants/types/chat';

export const updateContent = (id, editorState) => ({
  type: types.updateContent,
  payload: editorState,
  meta: id,
});

export const insertContent = (id, message, editorState) => ({
  type: types.updateContent,
  payload: editor.insertText(message, editorState || EditorState.createEmpty()),
  meta: id,
});

export const insertImages = (id, files, editorState) => ({
  type: types.updateContent,
  payload: Promise.all(
    files.map(file => new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        resolve(reader.result);
      };
      reader.onerror = reject;
      reader.readAsDataURL(file);
    }))
  )
  .then(results => results.map((result, i) => ({
    dataUrl: result,
    file: files[i],
  })))
  .then(data => data.reduce(
    (prev, curr) => editor.insertImage(prev, curr.dataUrl, { file: curr.file }),
    editorState
  )),
  meta: id,
});



