import React from 'react';
import Icon from 'src/components/Icons/Icon';
import { iconTypes } from 'src/constants/iconTypes';
import style from './TextEditor.css';

const SelectionLine = ({
  text,
  selected,
  onClick,
}) => (
  <div
    className={selected ? style.selectedLine : style.unselectedLine}
    onClick={!selected && onClick}
  >
    <Icon className={iconTypes.success} />
    <span>{text}</span>
  </div>
);

SelectionLine.propTypes = {
  text: React.PropTypes.string.isRequired,
  selected: React.PropTypes.bool,
  onClick: React.PropTypes.func.isRequired,
};

export default SelectionLine;



